import classes from "./Checkout.module.css";
import {useRef, useState} from "react";

const isEmpty = value => value.trim() === '';
const isNotFiveCharacters = value => value.trim().length !== 5;

const Checkout = props => {
  const [formInputsValidity, setFormInputsValidit] = useState({
    name: true,
    street: true,
    postal: true,
    city: true,
  });

  const nameInputRef = useRef();
  const streetInputRef = useRef();
  const postalInputRef = useRef();
  const cityInputRef = useRef();

  const submitHandler = (event) => {
    event.preventDefault();

    const enteredName = nameInputRef.current.value;
    const enteredNameIsValid = !isEmpty(enteredName);

    const enteredStreet = streetInputRef.current.value;
    const enteredStreetIsValid = !isEmpty(enteredStreet);

    const enteredPostal = postalInputRef.current.value;
    const enteredPostalIsValid = !isNotFiveCharacters(enteredPostal);

    const enteredCity = cityInputRef.current.value;
    const enteredCityIsValid = !isEmpty(enteredCity);

    setFormInputsValidit({
      name:enteredNameIsValid,
      street: enteredStreetIsValid,
      postal: enteredPostalIsValid,
      city: enteredCityIsValid,
    })

    const formIsValid = enteredNameIsValid &&
      enteredStreetIsValid &&
      enteredPostalIsValid &&
      enteredCityIsValid;

    if (!formIsValid) {
      return;
    }
    // submit cart data
    const userData = {
      name:enteredName,
      street:enteredStreet,
      postal:enteredPostal,
      city:enteredCity
    }

    props.onConfirm(userData)
  };

  return (
    <form className={classes.form} onSubmit={submitHandler}>
      <div className={`${ classes.control } ${ !formInputsValidity.name ? classes.invalid : '' }`}>
        <label htmlFor="name">Your Name</label>
        <input type="text" id="name" ref={nameInputRef} />
        {!formInputsValidity.name && <p>Please enter a valid name</p>}
      </div>
      <div className={`${ classes.control } ${ !formInputsValidity.street ? classes.invalid : ''}`}>
        <label htmlFor="street">Street</label>
        <input type="text" id="street" ref={streetInputRef} />
        {!formInputsValidity.street && <p>Please enter a valid street</p>}
      </div>
      <div className={`${ classes.control } ${ !formInputsValidity.postal ? classes.invalid : '' }`}>
        <label htmlFor="postal">Postal Code</label>
        <input type="text" id="postal" ref={postalInputRef} />
        {!formInputsValidity.postal && <p>Please enter a valid postal code (5 characters)</p>}
      </div>
      <div className={`${ classes.control } ${ !formInputsValidity.city ? classes.invalid : '' }`}>
        <label htmlFor="city">City</label>
        <input type="text" id="city" ref={cityInputRef} />
        {!formInputsValidity.city && <p>Please enter a valid city</p>}
      </div>
      <div className={classes.actions}>
        <button type="button" onClick={props.onCancel}>Cancel</button> {/* type 'button' will prevent submition */}
        <button className={classes.confirm}>Confirm</button> {/* this button will trigger the onSubmit */}
      </div>
    </form>
  );
};

export default Checkout;